import React, { Component } from 'react';
import {Link} from 'react-router-dom'

class Nav extends Component {

  state = {
    open: false
  }

  handleClick = () => {
    this.setState({open: !this.state.open})
  }
  render() {
    const handleClick = this.handleClick
    const OpenClass = (this.state.open) ? 'responsive' : ''
    const iconChar = (this.state.open) ? '\u2716' : '\u2630'

    return (
      <div className={`col col-right col-4 ${OpenClass}`}>
        <nav className="topnav">
          <Link to="/">Home</Link>
          <Link to="/wizard">Wizard</Link>
          <Link to="/my-profile/edit">My Profile</Link>
          <a rel="nofollow" data-method="delete" href="/users/sign_out">Logout</a>
          <a href="#open-nav" className="icon" onClick={handleClick}>{iconChar}</a>
        </nav>
      </div>
    );
  }
}

export default Nav;



